.btn {
  transition: background-color 0.5s ease;
}
.btn span {
  color: #fff;
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  transition: color 0.5s ease;
}

.btn-def {
  border-radius: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 48px;
  background-color: #244c98;
  padding: 12px 12px 12px 24px;
  border: 1px solid #244c98;
}
.btn-def__icon {
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border-radius: 50%;
  transition: background-color 0.5s ease;
}
.btn-def__icon svg {
  width: 24px;
  height: 24px;
}
.btn-def__icon svg path {
  transition: stroke 0.5s ease;
}
.btn-def:hover {
  background-color: #fff;
}
.btn-def:hover span {
  color: #244c98;
}
.btn-def:hover .btn-def__icon {
  background-color: #244c98;
}
.btn-def:hover .btn-def__icon svg path {
  stroke: #fff;
}

.btn-sec {
  background-color: #244c98;
  width: 100%;
  padding: 17px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #244c98;
  border-radius: 4px;
}
@media (any-hover: hover) {
  .btn-sec:hover {
    background-color: transparent;
  }
  .btn-sec:hover span {
    color: #244c98;
  }
}
.btn-sec--white {
  background-color: transparent;
}
.btn-sec--white span {
  color: #244c98;
}
@media (any-hover: hover) {
  .btn-sec--white:hover {
    background-color: #244c98;
  }
  .btn-sec--white:hover span {
    color: #fff;
  }
}

@media (max-width: 1400px) {
  .btn span {
    font-size: 15px;
    line-height: 24px;
  }
  .btn-def {
    column-gap: 20px;
    padding: 10px 10px 10px 18px;
  }
  .btn-def__icon {
    width: 36px;
    height: 36px;
  }
}
.modals-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -4;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  top: 0;
  left: 0;
  transition: opacity 0.2s ease, visibility 0.2s ease, z-index 0.2s ease;
}
.modals-overlay.active {
  z-index: 4;
  opacity: 1;
  visibility: visible;
}

.modal-auth {
  padding: 24px;
  border-radius: 10px;
  background-color: #ebf0fa;
  width: 500px;
  transition: opacity 0.2s ease, visibility 0.2s ease;
  opacity: 0;
  visibility: hidden;
}
.modal-auth.active {
  opacity: 1;
  visibility: visible;
}
.modal-auth__close {
  width: 52px;
  height: 52px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  transition: background-color 0.5s ease;
  border-radius: 10px;
  margin: 0 0 0 auto;
}
.modal-auth__close svg {
  width: 24px;
  height: 24px;
}
.modal-auth__close svg path {
  transition: stroke 0.5s ease;
}
@media (any-hover: hover) {
  .modal-auth__close:hover {
    background-color: #000;
  }
  .modal-auth__close:hover svg path {
    stroke: #fff;
  }
}
.modal-auth__title {
  font-size: 24px;
  font-weight: 500;
  line-height: 32.78px;
  margin-bottom: 18px;
  color: #323232;
}
.modal-auth__btns {
  display: flex;
  flex-direction: column;
  row-gap: 4px;
  margin-top: 36px;
}
.modal-auth__inputs {
  display: flex;
  flex-direction: column;
  row-gap: 4px;
  margin-bottom: 24px;
}
.modal-auth__checkbox {
  margin-bottom: 24px;
}
.modal-auth__link {
  font-size: 20px;
  font-weight: 500;
  line-height: 27.32px;
  color: #244c98;
  transition: color 0.5s ease;
}
@media (any-hover: hover) {
  .modal-auth__link:hover {
    color: #1f4284;
  }
}

.input {
  border-radius: 10px;
  background-color: #d7e2f6;
  padding: 24px;
  border: 0;
  width: 100%;
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  color: #244c98;
}
.input::placeholder {
  font-size: 18px;
  font-weight: 500;
  line-height: 28px;
  color: #1f4284;
}

.checkbox {
  display: flex;
  column-gap: 14px;
  align-items: center;
  cursor: pointer;
}
.checkbox label {
  font-size: 18px;
  font-weight: 400;
  line-height: 24.59px;
  display: flex;
  column-gap: 14px;
  align-items: center;
  position: relative;
  cursor: pointer;
  transition: color 0.5s ease;
}
.checkbox label::before {
  content: "";
  display: block;
  border-radius: 2px;
  border: 2px solid #000;
  width: 18px;
  height: 18px;
  transition: border-color 0.2s ease;
  flex-shrink: 0;
  transition: border-color 0.5s ease;
}
@media (any-hover: hover) {
  .checkbox:hover label {
    color: #244c98;
  }
  .checkbox:hover label::before {
    border-color: #244c98;
  }
}
.checkbox input {
  display: none;
}
.checkbox input:checked + label {
  color: #244c98;
}
.checkbox input:checked + label::before {
  background: url("../assets/img/svg/check.svg");
  background-repeat: no-repeat;
  background-position: center center;
  border-color: #1f4284;
}